import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';
import Card from './Card'

class DeckOfCards extends React.Component {

    constructor() {
        super();
        this.returnCards = this.returnCards.bind(this);
        console.log('my m y', utils.newDeck());

        this.state = {
            deck: utils.newDeck()
        };
    }

    drop(ev) {
        ev.preventDefault();
        var data = ev.dataTransfer.getData("text");
        console.log(data)
        console.log(document.getElementById(data))
        ev.target.appendChild(document.getElementById(data));
    }

    allowDrop(ev) {
        ev.preventDefault();
    }

    onDragStart(ev) {
        console.log('id',ev.target.id)
        ev.dataTransfer.setData("text", ev.target.id);
    }

    returnCards(data, itr) {
        let card = <Card indexId={'card'+itr} onDragStart={this.onDragStart}  value={data}/>;
        return (card)
    }

    componentDidMount() {
        let skip_height = $( '#cards-container' ).height();
        $('#cards-container').ready(function () {
            $('.card-container').each(function () {

                let holder = $(this).parent();
                let divWidth = holder.width();
                let divHeight = holder.height();

                $(this).css({
                    'left': Math.floor(Math.random() * Number(divWidth)),
                    'top': (Math.floor(Math.random() * Number(divHeight))) + (skip_height/2.5)
                });

            })
        });
    }

    render() {
        return (
            <div className="main-container">
                <div className="child-30">
                    <div className="div-by-4">
                        <div className="card-holder" id="spades-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            <div className="empty-holder-b"><p className="empty-text">Place spades here</p>
                                <p>&spades;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="clubs-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            <div className="empty-holder-b"><p className="empty-text">Place clubs here</p>
                                <p>&clubs;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="hearts-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            <div className="empty-holder-r"><p className="empty-text">Place hearts here</p>
                                <p>&hearts;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="diamonds-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            <div className="empty-holder-r"><p className="empty-text">Place diamonds here</p>
                                <p>&diams;</p></div>
                        </div>
                    </div>

                </div>
                <div className="child-70" id="cards-container" onDrop={this.drop} onDragOver={this.allowDrop}>
                    {
                        this.state.deck.map(this.returnCards)
                    }
                </div>

                {/*<div style={{'width': 200, 'height': 200}} id="div1" onDrop={this.drop} onDragOver={this.allowDrop}>*/}

                {/*<p draggable="true" onDragStart={this.onDragStart} id="drag1" width="88"*/}
                {/*height="31">Hello</p>*/}

                {/*</div>*/}

                {/*<div style={{'width': 200, 'height': 200}} id="div2" onDrop={this.drop} onDragOver={this.allowDrop}></div>*/}
            </div>
        )
    }

}

ReactDOM.render(
    <DeckOfCards/>
    , document.getElementById('app'))


